                <!-- <del class="c_98">¥10000.00</del> -->
              </div>
              <div
                v-else
                class="w40 right"
              >¥{{ item.price * item.num }}
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 物料 -->
      <div
        class="p10"
        v-if="$route.query.applyType == '3' && materialInfo.length > 0"
      >
        <div class="top_title">
          <div class="title_left">
            <div
              class="c_title"
              style="background:#05B96C;"
            ></div>
            <div class="weight">物料</div>
          </div>
        </div>
        <div>
          <div
            v-for="(item,i) in materialInfo"
            :key='i'
          >
            <div
              v-if="item.price * item.num != '0'"
              class="flex p10"
            >
              <div class="w40">
                <div>{{ item.devName }}</div>
                <div class="c_98">¥{{ item.price }}/套</div>
              </div>
              <div class="c_98 w20">x{{ item.num }}</div>
              <div
                v-if="!$route.query.type"
                class="w40 right"
              >
                <div>¥{{ item.price * item.num }}</div>
              </div>
              <div
                v-else
                class="w40 right"
              >¥{{ item.price * item.num }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div
      class="flex totle_box"
      v-if="$route.query.applyType == '3' && lockInfo.length > 0 || devInfo.length > 0 || materialInfo.length > 0"
    >
      <div></div>
      <div>合计: <span style="color:red;">¥{{ totalPrice }}</span></div>
    </div>
    <!-- 合计支付 -->
    <van-field
      v-if="$route.query.applyType == '3' && lockInfo.length > 0 || devInfo.length > 0 || materialInfo.length > 0"
      v-model="realAmount"
      type="number"
      style="margin:10px 0 50px 0;"
      label="合计支付"
      :readonly="c_role == 'ROLE_AREA_GM'?true:false"
      placeholder="请输入合计金额"
    />
    <!-- 提交时间 -->
    <van-cell
      v-if="$route.query.applyType == '2' && dataInfo"
      v-model="dataInfo.createTime"
      style="margin: 10px 0 50px 0;"
      title="提交时间"
      readonly
    />
    <div
      class="flex bottom_box"
      style="text-align:center;"
      v-if="$route.query.role"
    >
      <div
        class="bottom_l"
        @click="showReject = true"
      >驳回</div>
      <div
        class="bottom_l bottom_r"
        @click="commitApply"
      >{{ $route.query.applyType == '2'?'下一步':c_role == 'ROLE_AREA_GM'?'通过申请':'提交区域审批' }}</div>
    </div>
    <!-- 时间 -->
    <van-popup
      v-model="showTime"
      round
      position="bottom"
    >
      <van-picker
        title="时间"
        show-toolbar
        :columns="times"
        @confirm="onConfirmTime"
        @cancel="showTime = false"
      />
    </van-popup>
    <!-- 添加房间 -->
    <van-popup
      v-model="showRooms"
      position="bottom"
      style="line-height: 50px;"
    >
      <div
        class="flex"
        v-if="addAllRooms.length > 0"
        style="border-bottom:4px solid #F6F3F6;"
      >
        <div
          class="flex"
          style="padding-left: 120px;"
        >
          <div>选择房间号</div>
          <div
            class="flex"
            @click="chooseAllRooms"
          >
            <div style="padding:0 10px 0 60px;">全选</div>
            <img
              v-if="allRoomsStatus == true"
              class="chooseIcon"
              src="@/assets/btn_selected.png"
            >
            <img
              v-else
              class="chooseIcon"
              style="opacity:0.2;"
              src="@/assets/btn_normal.png"
            >
          </div>
        </div>
      </div>
      <div
        class="room_box_scroll"
        v-if="addAllRooms.length > 0"
      >
        <div
          v-for="(item,i) in addAllRooms"
          :key="i"
          @click="chooseRoom(item)"
          class="room_info"
        >
          <div style="padding-right: 20px;">{{ item.num }}</div>
          <img
            v-if="item.status == true"
            class="chooseIcon"
            src="@/assets/btn_selected.png"
          >
          <img
            v-else
            class="chooseIcon"
            style="opacity:0.2;"
            src="@/assets/btn_normal.png"
          >
        </div>
      </div>
      <div
        v-else
        style="color:red;line-height:80px;"
      >暂无房间,请帮助酒店新建房间号</div>
      <div
        class="flex"
        style="border-top:4px solid #F6F3F6;"
      >
        <div
          class="border_eee"
          :style="{width:addAllRooms.length > 0?'50%':'100%'}"
          @click="showRooms = false"
        >{{ addAllRooms.length > 0?'取消':'确定' }}</div>
        <div
          class="border_eee"
          style="color:#f60;"
          v-if="addAllRooms.length > 0"
          @click="choosedRooms"
        >确定</div>
      </div>
    </van-popup>
    <!-- 驳回理由 -->
    <van-dialog
      v-model="showReject"
      show-cancel-button
      :before-close="confirmReject"
    >
      <van-field
        v-model="auditFailReason"
        rows="3"
        type="textarea"
        style="text-align:left;"
        placeholder="请简述驳回原因"
      />
    </van-dialog>
    <!-- 展示照片 -->
    <van-popup
      v-model="show_pic"
      style="width:90%;height:50%;"
    >
      <img
        :src="open_pic"
        style="width:100%;height:100%;"
      >
    </van-popup>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotel } from '@/api/api_hotel.js';
import { GetBusinessAuditApproval, GetBusinessHotelLockInstallByaudit, PostBusinessAuditApprovalRefuse, PostBusinessHotelLockInstallEdit, PostBusinessAuditApprovalCommit } from '@/api/api_equip';
import { Toast } from 'vant';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            lockInfo: [],
            devInfo: [],
            materialInfo: [],
            hotelPhotos: [],
            businessPhotos: [],
            idCordPhotos: [],
            allRooms: [],
            hotelInfo: null,
            address: null,
            value: null,
            machineType: '请选择型号',
            airEle: '请选择型号',
            totalPrice: null,
            showRooms: false,
            allRoomsStatus: false,
            showTime: false,
            showReject: false,
            show_pic: false,
            auditFailReason: null,
            time: null,
            realAmount: null,
            dataInfo: null,
            orderInfo: null,
            otherInfo: null,
            role_area: null,
            c_role: null,
            open_pic: null,
            times: [],
            lockTypes: {},
            doorInstalls: [],
            doorTypes: [],
            addAllRooms: []
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.type ? '编辑申请单' : '申请详情');
        this.c_role = this.$route.query.role;
        this.getRoleArea();
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'business_lock_install_type' }).done((res) => {
                this.lockTypesAll = res.data;
                this.lockTypes = {};
                res.data.map((item) => {
                    this.lockTypes[item.dictValue] = item.dictLabel;
                });
            });
            this.time = dayjs().year();
            let thisYear = dayjs().year();
            for (let i = 0; i <= 5; i++) {
                this.times.push(thisYear--);
            }
        },
        getRoleArea() {
            GetBusinessAuditApproval({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                if (this.$route.query.applyType == '2') {
                    this.orderInfo = res.order;
                } else if (this.$route.query.applyType == '3') {
                    this.orderInfo = res.other || '';
                    this.getLockInfo();
                    this.getType();
                }
                this.dataInfo = res.data; // 流程信息
                let auditApprovalInfo = {
                    auditApprovalId: this.$route.query.auditApprovalId,
                    applyUser: this.dataInfo.applyUser.realName || this.dataInfo.applyUser.nickName || this.dataInfo.applyUser.userName,
                    createTime: this.dataInfo.createTime
                };
                localStorage.setItem('auditApprovalInfo', JSON.stringify(auditApprovalInfo));
                this.role_area = this.orderInfo.contacts + ' ' + this.orderInfo.phone;
                this.address = this.orderInfo.province + this.orderInfo.city + this.orderInfo.county;
                let obj_0 = {
                    title: '前台照片',
                    icon: this.orderInfo.receptionImage ? this.orderInfo.domainName + this.orderInfo.receptionImage : ''
                };
                this.hotelPhotos.push(obj_0);
                let obj_1 = {
                    title: '门头照片',
                    icon: this.orderInfo.frontDoorImage ? this.orderInfo.domainName + this.orderInfo.frontDoorImage : ''
                };
                this.hotelPhotos.push(obj_1);
                let obj_2 = {
                    title: '门锁照片',
                    icon: this.orderInfo.lockImage ? this.orderInfo.domainName + this.orderInfo.lockImage : ''
                };
                this.hotelPhotos.push(obj_2);
                let obj_3 = {
                    title: '房间照片',
                    icon: this.orderInfo.roomImage ? this.orderInfo.domainName + this.orderInfo.roomImage : ''
                };
                this.hotelPhotos.push(obj_3);
                let obj_4 = {
                    title: '营业执照',
                    icon: this.orderInfo.licenseImage ? this.orderInfo.domainName + this.orderInfo.licenseImage : ''
                };
                this.businessPhotos.push(obj_4);
                let obj_5 = {
                    title: '消防许可证',
                    icon: this.orderInfo.fireConImage ? this.orderInfo.domainName + this.orderInfo.fireConImage : ''
                };
                this.businessPhotos.push(obj_5);
                let obj_6 = {
                    title: '特行证',
                    icon: this.orderInfo.specialImage ? this.orderInfo.domainName + this.orderInfo.specialImage : ''
                };
                this.businessPhotos.push(obj_6);
                let obj_7 = {
                    title: '身份证',
                    icon: this.orderInfo.idFrontImage ? this.orderInfo.domainName + this.orderInfo.idFrontImage : ''
                };
                this.businessPhotos.push(obj_7);
                let obj_8 = {
                    icon: this.orderInfo.idFrontImage ? this.orderInfo.domainName + this.orderInfo.idFrontImage : ''
                };
                this.idCordPhotos.push(obj_8);
                let obj_9 = {
                    icon: this.orderInfo.idBackImage ? this.orderInfo.domainName + this.orderInfo.idBackImage : ''
                };
                this.idCordPhotos.push(obj_9);
            });
        },
        // 根据id查询酒店账户详细信息
        getCurrentInfo() {
            GetBusinessHotel({ hotelId: this.$route.query.applyHotelId }).done((res) => {
                this.hotelInfo = res.data;
                this.address = this.hotelInfo.province + this.hotelInfo.city + this.hotelInfo.county;
                this.getLockInfo();
            });
        },
        // 根据流程查询门锁工单信息
        getLockInfo() {
            GetBusinessHotelLockInstallByaudit({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                if (res.data) {
                    this.lockInfo = res.data.lockInfo ? JSON.parse(res.data.lockInfo) : '';
                    if (this.lockInfo) {
                        let orderKey = [];
                        let newMyOrder = [];
                        this.lockInfo.forEach((order) => {
                            let orderIndex = orderKey.indexOf(order.lockType);
                            if (orderIndex == -1) {
                                orderKey.push(order.lockType);
                                newMyOrder.push({
                                    lockType: order.lockType,
                                    num: 1,
                                    price: order.price
                                });
                            } else {
                                newMyOrder[orderIndex].num++;
                            }
                        });
                        this.lockInfo = newMyOrder;
                    }
                    this.devInfo = res.data.devInfo ? JSON.parse(res.data.devInfo) : '';
                    this.materialInfo = res.data.materialInfo ? JSON.parse(res.data.materialInfo) : '';
                    this.totalPrice = res.data.totalPrice;
                    this.realAmount = this.$route.query.type ? res.data.totalPrice : res.data.realAmount;
                    this.lockInfoInstall = {
                        id: res.data.id,
                        lockInfo: res.data.lockInfo,
                        devInfo: res.data.devInfo,
                        materialInfo: res.data.materialInfo,
                        totalPrice: res.data.totalPrice,
                        realAmount: this.realAmount
                    };
                }
            });
        },
        // 选择时间
        onConfirmTime(value) {
            this.time = value;
            this.showTime = false;
        },
        //
        onConfirm() {},
        // 添加房间
        openShowRooms() {
            this.showRooms = true;
        },
        // 全选-房间
        chooseAllRooms() {
            this.addAllRooms.map((item) => {
                item.status = this.allRoomsStatus == true ? false : true;
            });
            this.allRoomsStatus = !this.allRoomsStatus;
        },
        // 选择-房间
        chooseRoom(item) {
            this.addAllRooms.map((itm) => {
                if (itm.num == item.num) {
                    itm.status = !itm.status;
                }
            });
        },
        // 选中-房间
        choosedRooms() {
            let isHave = this.addAllRooms.some((item) => {
                return item.status == true;
            });
            if (isHave == false) {
                Toast('至少选择一个房间号');
            } else {
                let aaa = this.addAllRooms.filter((item) => {
                    if (item.status == true) {
                        item.lockType = '锁类型';
                        item.orderType = '装锁方式';
                        item.openType = '开门类型';
                        return item;
                    }
                });
                this.allRooms = this.allRooms.concat(aaa);
                this.showRooms = false;
            }
        },
        // 佣金正则
        formatter(value) {
            if (value < 0 || value > 99) {
                Toast('请输入0-99之间');
                return '';
            } else {
                return value.replace(/^(\-)*(\d+)\.(\d).*$/, '$1$2.$3');
            }
        },
        // 驳回
        confirmReject(action, done) {
            if (action == 'confirm') {
                if (!this.auditFailReason) {
                    Toast('请输入驳回原因');
                    done(false);
                    return;
                }
                let data = {
                    auditApprovalId: this.$route.query.auditApprovalId,
                    auditFailReason: this.auditFailReason
                };
                PostBusinessAuditApprovalRefuse(data)
                    .done((res) => {
                        Toast.success(res.msg);
                        this.showReject = false;
                        this.auditFailReason = null;
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 900);
                        done(false);
                    })
                    .then((e) => {
                        this.auditFailReason = null;
                        this.showReject = false;
                        done(false);
                    });
            } else if (action == 'cancel') {
                done(false);
                this.auditFailReason = null;
                this.showReject = false;
            }
        },
        // 保存信息
        commitKeep() {},
        // 提交区域审批
        commitApply() {
            if (this.$route.query.applyType == '3') {
            }
            if (this.$route.query.applyType == '2') {
                this.$router.push({
                    path: '/soft-use',
                    query: {
                        auditApprovalId: this.$route.query.auditApprovalId,
                        hotelName: this.orderInfo.hotelName
                    }
                });
            } else if (this.$route.query.applyType == '3') {
                if (this.$route.query.role == 'ROLE_AREA_GM') {
                    PostBusinessAuditApprovalCommit({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                        Toast.success(res.msg);
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 900);
                    });
                } else {
                    if (!this.realAmount) {
                        Toast('请输入合计支付');
                        return false;
                    } else {
                        this.lockInfoInstall.realAmount = this.realAmount;
                    }
                    PostBusinessHotelLockInstallEdit(this.lockInfoInstall).done((res_lock) => {
                        PostBusinessAuditApprovalCommit({ auditApprovalId: this.$route.query.auditApprovalId }).done((res_audi) => {
                            Toast.success(res_audi.msg);
                            setTimeout(() => {
                                this.$router.go(-2);
                            }, 900);
                        });
                    });
                }
            }
        },
        // 展示照片
        seePic(item) {
            this.open_pic = item.icon;
            this.show_pic = true;
        }
    }
};
</script>
